body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100%;
  height: 100%;
  text-align: center;
}

.GameArea {
  background-image: url(/typing-game/static/media/dirt.abdbe1ad.jpg); 
  background-repeat: no-repeat;
  background-size:cover; 
  position: absolute;
  width: 100%;
  height:100%;
}

.Type {
  position: absolute;
  top: 62%;
  left: 45%;
}

.Score {
  position: absolute;
  top:5%;
  left: 80%;
  z-index: 2;
  color:white;
  -webkit-text-emphasis: bold;
          text-emphasis: bold;
}
.GameOver {
  z-index: 5;
  top: 20%;
  left: 40%; 
  font-size: 4em;
  color: red;
}
.Player {
  position: absolute;
  top:50%;
  left: 49%;
  background-image: url(/typing-game/static/media/character-left.8f00f941.png);
  background-size: contain;
  background-repeat: no-repeat;
  height: 10vh;
  width: 10vh;
}

.Monster {
  position: absolute;
  transition: 500ms;
}

.MonsterImg {
  height: 10vh;
  width: 10vh;
  background-image: url(/typing-game/static/media/zombie2.f0720a5c.png);
  background-size: contain;
  background-repeat: no-repeat;
}

.MonsterName {
  position: absolute;
  color:white;
  background-color: black;
  font-size: 110%;
  top: 40%;
}


